'use client';
import React, { useEffect } from 'react';
import dayjs from 'dayjs';
import { DiscoverHeaderSearch } from './discover-header.style';
import { useAtom, useAtomValue, useSetAtom } from 'jotai';
import { DataSourcePicker, getDataSourceSrv } from '@grafana/runtime';
import { css } from '@emotion/css';
import {
    indexesAtom,
    searchTypeAtom,
    discoverCurrentAtom,
    locationAtom,
    currentClusterAtom,
    tableFieldsAtom,
    timeFieldsAtom,
    currentDateAtom,
    currentTimeFieldAtom,
    currentIndexAtom,
    searchFocusAtom,
    datasourcesAtom,
    selectedDatasourceAtom,
    timeRangeAtom,
    databasesAtom,
    tablesAtom,
    currentTableAtom,
    disabledOptionsAtom,
} from 'store/discover';
import { isValidTimeFieldType } from 'utils/data';
import { Select, Field, useTheme2, TimeRangeInput } from '@grafana/ui';
import { FORMAT_DATE } from '../../../constants';
import { getDatabases, getFieldsService, getIndexesService, getTables } from 'services/metaservice';

export default function TracesHeader() {
    const catalog = 'internal';
    // const catalogs = useAtomValue(catalogAtom);
    const setIndexes = useSetAtom(indexesAtom);
    const [discoverCurrent, setDiscoverCurrent] = useAtom(discoverCurrentAtom);
    if (process.env.NODE_ENV !== 'production') {
        discoverCurrentAtom.debugLabel = 'current';
    }
    const [loc, setLoc] = useAtom(locationAtom);
    const [currentCluster, setCurrentCluster] = useAtom(currentClusterAtom);
    const setTableFields = useSetAtom(tableFieldsAtom);
    const [timeFields, setTimeFields] = useAtom(timeFieldsAtom);
    const [currentDate, setCurrentDate] = useAtom(currentDateAtom);
    const currentTimeField = useAtomValue(currentTimeFieldAtom);
    const [currentIndex, setCurrentIndex] = useAtom(currentIndexAtom);
    const searchFocus = useAtomValue(searchFocusAtom);
    const [selectedDatasource, setSelectedDatasource] = useAtom(selectedDatasourceAtom);
    const [timeRange, setTimeRange] = useAtom(timeRangeAtom);
    const [currentTable, setCurrentTable] = useAtom(currentTableAtom);
    const [databases, setDatabases] = useAtom(databasesAtom);
    const [tables, setTables] = useAtom(tablesAtom);
    const [datasources, setDataSource] = useAtom(datasourcesAtom);
    const setDisabledOptions = useSetAtom(disabledOptionsAtom);

    const selectdbDS = useAtomValue(selectedDatasourceAtom);

    useEffect(() => {
        const datasources = getDataSourceSrv().getList();
        setDataSource(datasources);
    }, [setDataSource]);

    useEffect(() => {
        if (currentIndex.length > 0) {
            setDisabledOptions([]);
        } else {
            setDisabledOptions(['Search']);
        }
    }, [currentIndex, setDisabledOptions]);

    const theme = useTheme2();

    useEffect(() => {
        if (selectdbDS) {
            const res = getDatabases(selectdbDS);
            res.subscribe({
                next: ({ data, ok }: any) => {
                    if (ok) {
                        const value = data?.results[0]?.data?.values[0];

                        if (value) {
                            const options = value.map((item: any) => {
                                return {
                                    label: item,
                                    value: item,
                                };
                            });
                            setDatabases(options);
                        }
                    }
                },
                error: err => {
                    console.log('', err);
                },
            });
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [selectdbDS]);

    // useEffect(() => {
    //     if (startTime && endTime) {
    //         setCurrentDate([dayjs(startTime), dayjs(endTime)]);
    //     }
    // }, [startTime, endTime, setCurrentDate]);

    function getFields(selectedTable: any) {
        getFieldsService({
            selectdbDS,
            database: discoverCurrent.database,
            table: selectedTable.value,
        }).subscribe({
            next: ({ data, ok }: any) => {
                if (ok) {
                    const value = data?.results[0]?.data?.values[0];
                    const fieldTypes = data?.results[0]?.data?.values[1];

                    const tableFields = data?.results[0]?.data?.values[0].map((item: any, index: number) => {
                        return {
                            label: item,
                            Field: item,
                            value: item,
                            Type: fieldTypes[index],
                        };
                    });

                    setTableFields(tableFields);

                    if (value) {
                        const options = value
                            .filter((field: any, index: number) => {
                                return isValidTimeFieldType(fieldTypes[index].toUpperCase());
                            })
                            .map((item: any) => {
                                return {
                                    label: item,
                                    value: item,
                                };
                            });
                        setDiscoverCurrent({
                            ...discoverCurrent,
                            timeField: options[0]?.value || '',
                        });
                        setTimeFields(options);
                    }
                }
            },
            error: (err: any) => {
                console.log('', err);
            },
        });
    }

    function getIndexes(selectedTable: any) {
        getIndexesService({
            selectdbDS,
            database: discoverCurrent.database,
            table: selectedTable.value,
        }).subscribe({
            next: ({ data, ok }: any) => {
                if (ok) {
                    const value = data?.results[0]?.data?.values[2];
                    const columnNames = data?.results[0]?.data?.values[4];
                    const indexesTypes = data?.results[0]?.data?.values[10];

                    if (!value || value.length === 0) {
                        setIndexes([]);
                        setCurrentIndex([]);
                        return;
                    }

                    const tableIndexes = value?.map((item: any, index: number) => {
                        return {
                            label: item,
                            value: item,
                            type: indexesTypes[index],
                            columnName: columnNames[index],
                        };
                    });

                    setIndexes(tableIndexes);

                    if (tableIndexes) {
                        setCurrentIndex(tableIndexes);
                    }
                }
            },
            error: (err: any) => {
                console.log('', err);
            },
        });
    }

    return (
        <div
            className={css`
                padding: 1rem;
                padding-top: 1.5rem;
                background-color: ${theme.isDark ? 'rgb(24, 27, 31)' : '#FFF'};
                display: flex;
                border-radius: 0.25rem 0.25rem 0 0;
                border-bottom: 1px solid ${theme.colors.border.medium};
            `}
        >
            <DiscoverHeaderSearch>
                <Field label="Datasource">
                    {/* filter  */}
                    <DataSourcePicker
                        width={20}
                        type={'mysql'}
                        current={selectedDatasource}
                        placeholder="Choose"
                        noDefault
                        filter={ds => ds.type === 'mysql'}
                        onChange={item => {
                            console.log('item', item);
                            setSelectedDatasource(item);
                        }}
                    />
                </Field>
                {/*  */}
                <Field label="Database" style={{ marginLeft: 8 }}>
                    <Select
                        width={15}
                        options={databases}
                        value={discoverCurrent.database}
                        onChange={(selectedDatabase: any) => {
                            setDiscoverCurrent({
                                ...discoverCurrent,
                                database: selectedDatabase.value,
                            });
                            getTables({
                                selectdbDS,
                                database: selectedDatabase.value,
                            }).subscribe({
                                next: ({ data, ok }: any) => {
                                    if (ok) {
                                        const value = data?.results[0]?.data?.values[0];

                                        if (value) {
                                            const options = value.map((item: any) => {
                                                return {
                                                    label: item,
                                                    value: item,
                                                };
                                            });
                                            setTables(options);
                                        }
                                    }
                                },
                                error: (err: any) => {
                                    console.log('', err);
                                },
                            });
                        }}
                    ></Select>
                </Field>

                <Field label="Table" style={{ marginLeft: 8 }}>
                    <Select
                        options={tables}
                        width={15}
                        value={currentTable}
                        onChange={(selectedTable: any) => {
                            setDiscoverCurrent({
                                ...discoverCurrent,
                                table: selectedTable.value,
                            });
                            setCurrentTable(selectedTable.value);
                            getFields(selectedTable);
                            getIndexes(selectedTable);
                        }}
                    />
                </Field>
            </DiscoverHeaderSearch>
            {!searchFocus && (
                <>
                    <Field label="">
                        <Select
                            value={currentTimeField}
                            options={timeFields}
                            onChange={(selectdbTimeFiled: any) => {
                                setDiscoverCurrent({
                                    ...discoverCurrent,
                                    timeField: selectdbTimeFiled.value,
                                });
                                setLoc((prev: any) => {
                                    const searchParams = prev.searchParams;
                                    searchParams?.set('timeField', selectdbTimeFiled.value);
                                    return {
                                        ...prev,
                                        searchParams,
                                    };
                                });
                            }}
                            placeholder={''}
                        />
                    </Field>
                    <Field label="" style={{ marginLeft: 8, marginRight: 8 }}>
                        <TimeRangeInput
                            isReversed={false}
                            onChange={timeRange => {
                                const start = dayjs(timeRange.from.toDate());
                                const end = dayjs(timeRange.to.toDate());
                                setLoc(prev => {
                                    const searchParams = prev.searchParams;
                                    searchParams?.set('startTime', start.format(FORMAT_DATE));
                                    searchParams?.set('endTime', end.format(FORMAT_DATE));
                                    return {
                                        ...prev,
                                        searchParams,
                                    };
                                });
                                setCurrentDate([start, end]);
                                setTimeRange(timeRange);
                            }}
                            value={timeRange}
                        />
                    </Field>
                </>
            )}
        </div>
    );
}
