import dayjs, { Dayjs, ManipulateType } from "dayjs";

export interface QueryTableDataParams {
    catalog: string;
    database: string;
    table: string;
    cluster: string;
    startDate: string;
    endDate: string;
    sort: string;
    timeField: string;
    interval: IntervalEnum;
}

export interface DataFilterType {
    fieldName: string;
    operator: Operator;
    value: Array<string | number>;
    label?: string;
    id: string;
}

export type Operator = '=' | '!=' | 'in' | 'not in' | 'is null' | 'is not null' | 'between' | 'not between' | 'like' | 'not like' | 'match_all' | 'match_any' | 'match_phrase';

export interface DiscoverCurrent {
    catalog: string;
    database: string;
    table: string;
    cluster: string;
    timeField: string;
    date: [Dayjs, Dayjs] | [];
}

export enum IntervalEnum {
    Auto = 'auto',
    Day = 'day',
    Week = 'week',
    Month = 'month',
    Year = 'year',
    Hour = 'hour',
    Minute = 'minute',
    Second = 'second',
}


export type ShortcutItem = {
    key: string;
    text: string;
    label: string;
    range: (now?: dayjs.Dayjs) => dayjs.Dayjs[];
    format: string;
    type: string;
    number: number;
}

export interface QueryTableFieldsParams {
    catalog: string;
    database: string;
    table: string;
    describe_extend_variant_column?: string;
}

export type FieldNode = {
    name: string;
    type: string;
    children?: FieldNode[];
    Null?: string;
    Key?: string;
    Default?: string | null;
    Extra?: string;
    Comment?: string;
    Visible?: string;
};


export interface RequestParams {
    catalog: string;
    database: string;
    table: string;
    describe_extend_variant_column?: boolean;
}

export interface QueryTableIndexesParams {
    catalog: string;
    database: string;
    table: string
}

export interface QueryTableParams {
    catalog: string;
    database: string;
}

export type AutoInterval = {
    current_date_range: number;
    current_type: ManipulateType;
    interval_unit: ManipulateType;
    interval_value: number;
};


export type Frame = {
    schema: {
        fields: Array<{ name: string }>;
    };
    data: {
        values: any[][];
    };
};

export type Result = {
    frames: Frame[];
};

export type Root = {
    results: Record<string, Result>;
};
