import { LoadingState } from '@grafana/data';
import { PanelRenderer } from '@grafana/runtime';
import { Drawer } from '@grafana/ui';
import { useAtom, useAtomValue } from 'jotai';
import React, { useEffect } from 'react';
import { getTableDataTraceService } from 'services/discover';
import { currentTableAtom, currentCatalogAtom, currentDatabaseAtom, tableTracesDataAtom, selectedDatasourceAtom, selectedRowAtom } from 'store/discover';
import { formatTracesResData } from 'utils/data';

export default function TraceDetail(props: { onClose?: () => void; open: boolean; traceId?: string }) {
    const currentTable = useAtomValue(currentTableAtom);
    const currentCatalog = useAtomValue(currentCatalogAtom);
    const currentDatabase = useAtomValue(currentDatabaseAtom);
    const [traceData, setTraceData] = useAtom(tableTracesDataAtom);
    const selectedRow = useAtomValue(selectedRowAtom);
    const selectdbDS = useAtomValue(selectedDatasourceAtom);

    const {open, traceId} = props;

    const getTraceData = React.useCallback(() => {
        let payload: any = {
            catalog: currentCatalog,
            database: currentDatabase,
            table: currentTable || 'otel_traces',
            cluster: '',
            sort: 'DESC',
            trace_id: traceId || '',
        };

        getTableDataTraceService({
            selectdbDS,
            ...payload,
        }).subscribe({
            next: ({ data, ok }: any) => {
                if (ok) {
                    const formatedData = formatTracesResData(data);
                    setTraceData(formatedData);
                }
            },
            error: (err: any) => {
                console.log('', err);
            },
        });
    }, [currentCatalog, currentDatabase, currentTable, selectdbDS, setTraceData, traceId]);

    useEffect(() => {
        if (traceId) {
            getTraceData();
        }
    }, [selectedRow.trace_id, currentCatalog, currentDatabase, selectdbDS, setTraceData, getTraceData, traceId]);

    return (
        <>
            {open && (
                <Drawer
                    title="Trace Panel"
                    onClose={() => {
                        props?.onClose?.();
                    }}
                    size="lg"
                >
                    {traceData ? (
                        <PanelRenderer
                            title="test"
                            width={200}
                            height={300}
                            pluginId="traces"
                            options={{}}
                            data={{
                                state: LoadingState.Done,
                                series: [traceData],
                                timeRange: {
                                    from: new Date(Date.now() - 15 * 60 * 1000) as any, // 15 
                                    to: new Date() as any,
                                    raw: {
                                        from: 'now-15m',
                                        to: 'now',
                                    },
                                },
                            }}
                        />
                    ) : null}
                </Drawer>
            )}
        </>
    );
}
