import { getBackendSrv } from '@grafana/runtime';
// import { lastValueFrom } from 'rxjs';

export function getDatabases(selectdbDS: any) {
    const response = getBackendSrv().fetch({
        url: '/api/plugins/selectdb-discover-app/resources/database',
        method: 'GET',
        params: {
            ds: selectdbDS.uid,
        },
        credentials: 'include',
    });
    return response;
    // const res = await lastValueFrom(response);
    // return res;
}

export function getTables({ selectdbDS, database }: { selectdbDS: any; database: string }) {
    const response = getBackendSrv().fetch({
        url: '/api/plugins/selectdb-discover-app/resources/table',
        method: 'GET',
        params: {
            ds: selectdbDS.uid,
            database,
        },
        credentials: 'include',
    });
    return response;
}

